<template><section><h1>描述</h1>
<p>提供了树组件，可以在节点上增加图标</p>
<h1>效果展示</h1>
<template>
  <div id="vtree-demo">
    <div class="trees">
      <div class="tree-box">
        <bs-tree v-model="checkedItem" :treeData="treeData1" :renderTitle="renderTitle" :isDrag="isDrag" @node-dblclick="dblclick">
      </bs-tree></div>
      <div class="tree-box">
        <input type="text" v-model="searchval" placeholder="请输入..." style="border:1px solid #999;">
        <bs-tree :treeData="treeData2" ref="tree" :multiple="true" :renderBtn="renderBtn">
      </bs-tree></div>
    </div>
    <div @click="getChecked">getChecked</div>
    <div>checkedItem：{{checkedItem}}</div>
    <div style="width:200px;height:100px;border:1px solid #ccc;" @dragover.prevent.stop="" @drop.prevent.stop="handleDrop">{{dragItem}}</div>
  </div>
</template>


<h1>代码示例</h1>
<pre v-pre=""><code v-pre="" class="language-html">

</code></pre>
<h1>props</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>属性名称</th>
<th>描述</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>treeData</td>
<td>组件接受的树形数据</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>multiple</td>
<td>是否多选模式</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>isDrag</td>
<td>是否可以拖拽</td>
<td>Function</td>
<td>(item) =&gt; false；(item当前节点对象)</td>
</tr>
<tr>
<td>renderTitle</td>
<td>自定义标题显示内容, 可以在标题前添加图标</td>
<td>Function</td>
<td>显示标题</td>
</tr>
<tr>
<td>renderBtn</td>
<td>自定义节点右侧按钮</td>
<td>Function</td>
<td>无</td>
</tr>
</tbody>
</table>
<p><strong>注:</strong><br>
<code v-pre="">renderTitle</code>, <code v-pre="">renderBtn</code>是通过render函数渲染, 其第二个参数有两个字段item(当前的节点)、index(当前节点的索引值)<br>
节点的高亮需求可以通过<code v-pre="">renderTitle</code>来处理<br>
节点的右侧控制按钮图标可以通过<code v-pre="">renderBtn</code>来处理<br></p>
<h1>tree属性</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>属性名称</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>当前选中的节点</td>
</tr>
</tbody>
</table>
<h1>treeData结构</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>id</td>
<td>节点id</td>
<td>String</td>
<td>N</td>
<td>—</td>
</tr>
<tr>
<td>title</td>
<td>节点名称</td>
<td>String</td>
<td>N</td>
<td>—</td>
</tr>
<tr>
<td>open</td>
<td>节点是否展开(可以通过addAttr方法添加)</td>
<td>Boolean</td>
<td>Y</td>
<td>false</td>
</tr>
<tr>
<td>checked</td>
<td>节点复选框是否选中(可以通过addAttr方法添加)</td>
<td>Boolean</td>
<td>Y</td>
<td>false</td>
</tr>
<tr>
<td>visible</td>
<td>节点是否可见(可以通过addAttr方法添加)</td>
<td>Boolean</td>
<td>Y</td>
<td>true</td>
</tr>
<tr>
<td>selected</td>
<td>节点是否被选中(可以通过addAttr方法添加)</td>
<td>Boolean</td>
<td>Y</td>
<td>false</td>
</tr>
<tr>
<td>pid</td>
<td>节点父id(可以通过addPid方法添加)</td>
<td>Boolean</td>
<td>Y</td>
<td>false</td>
</tr>
<tr>
<td>children</td>
<td>子节点</td>
<td>Array[Object]</td>
<td>Y</td>
<td>—</td>
</tr>
</tbody>
</table>
<h1>events</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>事件名称</th>
<th>描述</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>select-change</td>
<td>选中的值发生改变</td>
<td>args1:树形选中数据，完整上下级,args2:数组型选中数据，只有最底级的数据</td>
</tr>
<tr>
<td>node-dblclick</td>
<td>节点双击事件</td>
<td>当前节点</td>
</tr>
</tbody>
</table>
<h1>methods</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>方法名称</th>
<th>描述</th>
<th>参数</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>getNodes</td>
<td>通过属性查找节点</td>
<td>①查找条件②treeData(可选)</td>
<td>符合条件的节点组</td>
</tr>
<tr>
<td>searchNodes</td>
<td>搜索,过滤treeData</td>
<td>①过滤条件②treeData(可选)</td>
<td>无</td>
</tr>
<tr>
<td>addAttr</td>
<td>给treeData添加属性</td>
<td>①treeData②属性</td>
<td>无</td>
</tr>
<tr>
<td>addPid</td>
<td>给treeData添加pid</td>
<td>①treeData②根节点的pid(默认0)</td>
<td>无</td>
</tr>
</tbody>
</table>
</section></template>
<style scoped>
.trees {
  display: flex;
  justify-content: space-between;
}

.tree-box {
  width: 500px;
}
</style>
<script>
export default {
  name: 'Tree-demo',
  data() {
    return {
      checkedItem: {},
      dragItem: {},
      treeData1: [
        {
          id: 1,
          title: '一级-01',
          type: 1,
          children: [
            {
              id: 2,
              title: '二级-01',
              type: 1,
              children: [
                {
                  id: 3,
                  title: '三级-01',
                  type: 1
                }
              ]
            },
            {
              id: 4,
              title: '二级-02',
              type: 1
            }
          ]
        }
      ],
      treeData2: [
        {
          id: 1,
          title: '一级-01',
          type: 1,
          pid: 0,
          children: [
            {
              id: 2,
              title: '二级-01',
              type: 1,
              children: [
                {
                  id: 3,
                  title: '三级-01',
                  type: 1,
                  children: [
                    {
                      id: 31,
                      title: '四级-01',
                      type: 1
                    },
                    {
                      id: 32,
                      title: '四级-02',
                      type: 1
                    }
                  ]
                }
              ]
            },
            {
              id: 4,
              title: '二级-02',
              type: 1
            }
          ]
        }
      ],
      searchval: '',
      renderTitle: (h, ctx) => {
        return h('div', {
          class: {'searched': ctx.item.searched}
        }, '#' + ctx.item.title)
      },
      renderBtn: (h, {item, index}) => {
        if (!item.children) {
          return h('div', {
            class: {'item-right': true}
          }, [
            h('span', {
              on: {
                click: () => { this.print(item) }
              }
            }, '删除'),
            h('span', {
              on: {
                click: () => { this.alert(item) }
              }
            }, '打印')
          ])
        }
      },
      isDrag: (item) => {
        return !item.children
      }
    }
  },
  computed: {
  },
  watch: {
    searchval(val) {
      this.$refs.tree.searchNodes(val,this.treeData2)
      if (val === '') {
        this.$refs.tree.addAttr (this.treeData2, {open: false})
      }
    }
  },
  methods: {
    getChecked() {
      const answer2 = this.$refs.tree.getChecked({hasHalf: false})
      console.log('tree getChecked ans', answer2)
    },
    print(target) {
      console.log('你点击的是：', target)
    },
    alert(target) {
      alert('你点击的是：' + target.title)
    },
    dblclick(node) {
      console.log(node)
    },
    handleDrop(e) {
      this.dragItem = e.dataTransfer.getData('Text')
    }
  },
  mounted() {
    let attr = {
      open: false,
      checked: false,
      searched: false,
      visible: true
    }
    
    this.$refs.tree.addAttr (this.treeData1, attr)
    this.$refs.tree.addAttr (this.treeData2, attr)

    this.$refs.tree.addPid (this.treeData2)
  }
}
</script>


